.swiper {
  width: 100vw;
  // height:70vh;
  overflow: hidden;
}
@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
.swiper-slide-active .swiper-img,
.swiper-slide-duplicate-active .swiper-img,
.swiper-slide-prev .swiper-img {
  animation: zoomUp 7s linear 0s normal both;
}
.swiper-slide img {
  height: 100vh;
  width: 100%;
  object-fit: cover;
}
.swiper-wrapper {
  overflow: hidden;
}
